import { Component, OnInit } from '@angular/core';
import { NavController, AlertController, ModalController, LoadingController, ActionSheetController, PickerController, ToastController } from '@ionic/angular';
import { WitePrompt, AppMin } from "../core"
import { Router, ActivatedRoute } from '@angular/router';
import { DataserviceService } from '../dataservice.service';
// import { Wechat } from '@ionic-native/wechat/ngx';

// import { Alipay } from '@ionic-native/alipay/ngx';
// let alipay;
declare let cordova;
declare let Wechat;


@Component({
  selector: 'app-buy-service',
  templateUrl: './buy-service.page.html',
  styleUrls: ['./buy-service.page.scss'],
})

export class BuyServicePage implements OnInit {
  loader;
  user_select_service_id;
  touserid;

  sex = "";
  sexShow = "";

  //-------输出数据↓
  CName;
  serviceName;
  money: number = 0;
  start_money: number = 0;
  orderId;
  objlist: object;
  payType = ""; //支付方式 alipay or wechar

  gaming;


  alertButton = [];

  //----------选择属性

  select_couponId = 0;
  select_couponMoney;
  select_couponName = "不使用";


  //--------------输入属性
  userName: string = "";
  userAge: string = "";
  orderText = "";
  header_class;
  constructor(public navCtrl: NavController, public ToastController: ToastController, public PickerController: PickerController, public DataserviceService: DataserviceService, public router: Router, public ActivatedRoute: ActivatedRoute, public modalCtrl: ModalController, public loadingCtrl?: LoadingController, public actionSheetCtrl?: ActionSheetController, public alertCtrl?: AlertController, public appMin?: AppMin) {

    WitePrompt("收到的touserid" + this.ActivatedRoute.snapshot.queryParams['touserid']);


    if (appMin.GetPhoneMode() == "ios") {
      this.header_class = "header_class";
    }
    else {
      this.header_class = "header_class";
      console.log("判断为安卓系统 设置状态栏样式")
    }

    //this.touserid = this.navParams.get("touserid");//获取塔罗师id
    this.touserid = this.ActivatedRoute.snapshot.queryParams['touserid'];////获取塔罗师id
    //  this.user_select_service_id = this.navParams.get("serviceid");//获取用户选择的服务id
    this.user_select_service_id = this.ActivatedRoute.snapshot.queryParams['serviceid']//获取用户选择的服务id


  }



  ionViewWillEnter() {
    //每次即将进入页面

    this.addBuyData();

  }

  async send(value, isshow?: boolean) {


    if (isshow) {
      // -- old
      // this.loader = this.loadingCtrl.create({
      //   content: "加载中...",
      //   duration: 3000

      // });
      //  this.loader.present();

      //-- new 
      const loading = await this.loadingCtrl.create({
        message: '加载中...',
        duration: 3000
      });
      await loading.present();


    }

    this.appMin.post(JSON.parse(value)).subscribe((data) => {
      this.HandleData(data);
    })



  }

  baocl_go() {
    this.navCtrl.pop();
  }
  generateOrder() {
    //请求订单数据 
    //alert("generateOrder()发送生成订单");


    if (this.userName.trim() == "") {
      //必须填写姓名

      this.toast("必须填写姓名");

      return;
    }

    if (this.sex.trim() == "") {

      this.toast("请填写性别");
      return;
    }


    if (this.userAge == "" && !isNaN(parseInt(this.userAge))) {

      this.toast("请填写正确的年龄")
      return;
    }


    console.log("generateOrder2!")


    let obj = {
      type: 'generateOrder',
      serviceId: this.user_select_service_id,
      localUserId: this.appMin.getUserid(),
      toUserId: this.touserid,
      ouponId: this.select_couponId,
      userName: this.userName,
      userAge: this.userAge,
      userSex: this.sexShow,
      orderText: this.orderText,
      payType: this.payType,

    }
    this.send(JSON.stringify(obj), true);

  }
  async  shezsex() {
    //设置性别按钮
    WitePrompt("设置性别按钮被单击");
    const actionSheet = await this.actionSheetCtrl.create({
      // header: '',
      buttons: [
        {
          text: '男',
          // role: 'destructive',

          handler: () => {
            WitePrompt('男_click');
            this.sex = "0";
            this.sexShow = "男";
          }
        }, {
          text: '女',

          handler: () => {
            WitePrompt('女_click');
            this.sex = "1";
            this.sexShow = "女";
          }
        }, {
          text: '取消',
          role: 'cancel',

          handler: () => {
            WitePrompt('取消_click');
          }
        }
      ]
    });
    await actionSheet.present();


  }

  buyService(select?) {
    //提交订单按钮
    //this.generateOrder();
    //支付方式 alipay or wechar
    if (select == 'alipay') {
      //支付宝方式付款

      this.payType = "alipay";
      this.generateOrder();
    } else {
      //微信方式付款
      this.payType = "wechar";
      this.generateOrder();
    }

  }
  doRefresh(refresher) {
    console.log('Begin async operation', refresher);

    // setTimeout(() => {
    //     console.log('Async operation has ended');
    //     refresher.complete();
    // }, 1000);
    refresher.complete();
  }
  addBuyData() {
    //加载初始数据
    let obj = {
      type: 'addBuyData',
      serviceId: this.user_select_service_id,
      userId: this.appMin.getUserid()
    }
    this.send(JSON.stringify(obj), false);


  }
  HandleData(meg: any) {
    //处理返回的数据
    console.log('neg=' + JSON.stringify(meg));

    if (meg['type'] == "addBuyData_ret") {
      //更新数据完成 准备跳转
      this.CName = meg['CName'];
      this.serviceName = meg['serviceName'];
      this.money = meg['money'];
      this.start_money = meg['money'];
      // WitePrompt("this.money=" + this.money);
      this.objlist = meg['rows'];

      //this.alertButton;
      let shuzu = [];
      let count = meg['rows'].length;
      shuzu.push({
        label: '不使用',
        value: '0',
        item: {}
      });
      for (let i = 0; i < count; i++) {
        let objdata = {
          type: 'radio',
          label: meg['rows'][i]['couponName'] + " " + meg['rows'][i]['couponMoney'] + "￥",
          value: meg['rows'][i].couponId,
          item: meg['rows'][i]
          // checked: false,
          // handler: data => { this.selectCoupon(data) }

        }
        shuzu.push(objdata);

      }



      if (shuzu.length != 0) {
        this.gaming = shuzu[0].value;
      }

      this.alertButton = shuzu;

    }
    if (meg['type'] == "generateOrder_ret") {
      console.log("generateOrder_ret" + meg)
      this.orderId = meg['orderId'];
      // let a = meg['orderString'].split('&');
      // let appobj = {};
      // for (let i = 0; i < a.length; i++) {
      //   let b = a[i].split('=');
      //   let b1 = b[0];
      //   let b2 = b[1];
      //   WitePrompt(b1 + "=" + b2);

      //   appobj[b1] = b2;
      // }


      this.pay(meg['orderString']);

    }


  }
  async showRadio() {

    let alert = await this.PickerController.create({
      columns: []
      // buttons: [
      //   {
      //     text: '确定',

      //     cssClass: 'secondary',
      //     handler: () => {
      //       console.log('Confirm Cancel');
      //     }
      //   }
      // ]
    });

    await alert.present();
  }
  selectCoupon(val?) {

    if (this.gaming == '0') {
      console.log()
      WitePrompt("this.start_money=" + this.start_money);
      this.money = Number(this.start_money);
      return;
    }
    // console.log("gaming=" + this.gaming);
    var item = {};
    for (var i = 0; i < this.alertButton.length; i++) {
      if (this.alertButton[i]['item'].couponId == this.gaming) {
        item = this.alertButton[i]['item'];
      }
    }
    //  console.log(JSON.stringify(item));

    this.select_couponId = item['couponId'];

    this.select_couponMoney = item['couponMoney'];
    this.select_couponName = item['couponName'];
    let ok_money = this.start_money - this.select_couponMoney;
    if (ok_money <= 0) {
      this.money = 0;
    } else {
      this.money = Number(ok_money.toFixed(2));//保留小数后2位
    }
    // WitePrompt("this.money=" + this.money);
    // WitePrompt("this.start_money=" + this.start_money);
    // WitePrompt("this.select_couponName=" + this.select_couponName);


  }
  unescapeHTML(a) {
    let aNew = "" + a;
    return aNew.replace(/&lt;/g, "<").replace(/&gt;/g, ">").replace(/&amp;/g, "&").replace(/&quot;/g, '"').replace(/&apos;/g, "'");
  }

  pay(orderString) {
    WitePrompt("订单字符串=" + orderString);
    let this1 = this;
    if (this.payType == "alipay") {
      orderString = this.unescapeHTML(orderString);



      cordova.plugins.alipay.payment(orderString, (success) => {
        if (success.resultStatus === "9000") {
          console.log('支付成功')
          this1.toast("付款成功,开始咨询。")
          // console.log("buy_this1.touserid,="+this1.touserid)
          let obj = {
            toUserId: this1.touserid,//发送方id
            CName: this1.CName,
            UserImgUrl: this1.appMin.getUserImg(),
          }
          this1.router.navigate(['consult-time'], { queryParams: obj, replaceUrl: true });
        } else {
          console.log('支付失败')
        }
      }, (error) => {
        console.log('支付失败2')
      });
    } else {
      this1.toast("准备执行 调用微信方法。")
      let orderString_ = JSON.parse(orderString);


      var params = {
        partnerid: orderString_.mchid, // merchant id
        prepayid: orderString_.prepayid, // prepay id returned from server
        noncestr: orderString_.noncestr, // nonce string returned from server
        timestamp: orderString_.timestamp, // timestamp
        sign: orderString_.sign, // signed string
        appid: orderString_.appid //appid
      };




      console.log("asd0");

      try {


        Wechat.sendPaymentRequest(params, () => {
          this.toast("付款成功,开始咨询。")
          let obj = {
            toUserId: this1.touserid,//发送方id
            CName: this1.CName,
            UserImgUrl: this1.appMin.getUserImg(),
          }
          this.router.navigate(['consult-time'], { queryParams: obj, replaceUrl: true });

        }, (reason) => {
          alert("Failed: " + reason);
        });

      } catch (err) {
        console.log("asd1" + err);

      }


      console.log("asd2");


    }




    return;



  }
  async toast(msg) {
    let toast = await this.ToastController.create({
      message: msg,
      duration: 1500,
      position: 'top'
    });
    toast.present();
  }
  async paySelect() {

    //弹出选择付款方式按钮


    if (this.appMin.islogin() == false) {
      //先登录
      let alert = await this.alertCtrl.create({

        message: '请先登录，再继续',
        buttons: [
          {
            text: '取消',
            role: 'cancel',
            handler: () => {
              console.log('Cancel clicked');
            }
          },
          {
            text: '去登录',
            handler: () => {
              console.log('Buy clicked');
              let obj = {
                ispop: true

              }
              //this.navCtrl.push(login, obj);
              this.router.navigate(['/login', obj])
            }
          }
        ]
      });
      alert.present();
      return;
    }

    if (this.appMin.getUserid() == this.touserid) {
      let alert = await this.alertCtrl.create({
        header: '通知',
        subHeader: '不能购买自己的服务',
        buttons: ['确定']
      });
      return await alert.present();

    }




    if (this.appMin.islogin() == false) {
      console.log('没有登录 弹出提示')


      let confirm = await this.alertCtrl.create({

        message: '请先登录，再操作',
        buttons: [
          {
            text: '取消',
            handler: () => {
              console.log('Disagree clicked');
            }
          },
          {
            text: '去登陆',
            handler: () => {
              let obj = {
                ispop: true
              }
              // this.navCtrl.push(login, obj);
              this.router.navigate(['/login', obj])
            }
          }
        ]
      });
      await confirm.present();

      return;

    } else {
      console.log('已经登录 继续执行');
    }


    let actionSheet = await this.actionSheetCtrl.create({
      // header: '',
      buttons: [
        {
          text: '支付宝付款',
          // role: 'destructive',

          cssClass: 'iconfont',

          handler: () => {
            this.buyService('alipay');

          }
        }, {
          text: '微信支付',

          cssClass: 'iconfont',
          handler: () => {
            this.buyService('wechar');

          }
        }, {
          text: '取消',
          role: 'cancel',
          handler: () => {
            WitePrompt('取消_click');
          }
        }
      ]
    });
    await actionSheet.present();
  }

  ngOnInit() {


  }

}
